import styled from "styled-components";

export const StyledInput = styled.input`
    padding: 10px;
    width: 100%;
    max-width: 500px;
    border-radius: 2px;
    float: right;
    border: none;
    font-size: 16px;
    height: auto;
    background-color: #e8eeef;
    color: #8a97a0;
    box-shadow: 0 1px 0 #00000008 inset;
`;